import Imagem from "@/components/Imagem";

const SectionOne = () => {
    return (
        <div className="relative h-screen w-full overflow-hidden">
            <Imagem
                src="/assets/bg-image-hero.png"
                alt="Hero background"
                fill
                className="object-cover"
            />
        </div>
    );
};

export default SectionOne;
